<template>
   <div class="photo-container">
      <div class="photo-grid">
         <div class="photo-box" v-for="(photo, index) in photos" :key="index">
            <img :class="`photo photo-${photo}`"
                     :src="require(`../assets/Photo/Small/small-${photo}.png`)"
                     :alt="`Small Photo ${photo}`"
                     @click="largePhoto(photo)">
                     
         </div>
      </div>
      <div class="large-photo-container" @click.self="exitPhoto()" v-show="largePhotoToggle">
         <div class="large-photo-position">
            {{ selectedPhoto }} / 29
         </div>
         <img class="arrow arrow-previous"
                  src="../assets/Photo/left-arrow.png"
                  alt="Previous Button"
                  @click="previousPhoto()">

         <img class="arrow arrow-forward"
                  src="../assets/Photo/right-arrow.png"
                  alt="Forward Button"
                  @click="forwardPhoto()">

         <button class="exit-photo" @click="exitPhoto()">x</button>
         <div class="large-photo-box">
            <img class="large-photo"
                     :src="require(`../assets/Photo/Large/large-${selectedPhoto}.png`)"
                     :alt="`Large Photo ${selectedPhoto}`">

         </div>
      </div>
   </div>
</template>

/////////////////////////////////////////////////////////////////////////////////////////////////////////

<script>
export default {
   data() {
      return {
         photos: 29,
         selectedPhoto: 1,
         largePhotoToggle: false
      }
   },
   methods: {
      largePhoto(photo) {
         this.selectedPhoto = photo;
         this.largePhotoToggle = true;
         TweenMax.fromTo('.large-photo', .5, {scale: .5, x: '-50%', y: '-50%'}, {scale: 1, x: '-50%', y: '-50%'});
      },
      previousPhoto() {
         if (this.selectedPhoto <= 1) {
            this.selectedPhoto = 29;
         } else {
            this.selectedPhoto -= 1;
         }
      },
      forwardPhoto() {
         if (this.selectedPhoto >= 29) {
            this.selectedPhoto = 1
         } else {
            this.selectedPhoto += 1;
         }
      },
      exitPhoto() {
         this.largePhotoToggle = false;
      }
   }
}
</script>

/////////////////////////////////////////////////////////////////////////////////////////////////////////

<style scoped>
   /*******************************************************/
   /* Photo Grid */
   /*******************************************************/

   .photo-container {
      padding: .5rem;
      background-color: rgb(243, 255, 255);
   }

   .photo-grid {
      display: grid;
      overflow: hidden;
      grid-template-columns: repeat(auto-fill, minmax(25rem, 1fr));
      /* grid-template-columns: repeat(4, 25%);
      grid-template-rows: repeat(13, 25vw); */
      grid-gap: .5rem;
   }

   .photo-box {
      overflow: hidden;
   }

   .photo {
      width: 100%;
      height: 25rem;
      cursor: pointer;
      object-fit: cover;
      display: block;
   }
   .photo:hover {
      transition: .3s;
      transform: scale(1.05);
      filter: brightness(1.1);
   }

   /*******************************************************/
   /* Photo Gallery */
   /*******************************************************/

   .large-photo-container {
      z-index: 2;
      position: fixed;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background-color: rgba(0, 0, 0, .9);
   }

   .large-photo-position {
      position: absolute;
      margin: 1rem;
      font-size: 1rem;
      color: #eee;
   }

   .arrow {
      z-index: 1;
      position: absolute;
      cursor: pointer;
      height: 4rem;
      filter: brightness(.4);
      opacity: 0.5;
   }
   .arrow:hover {
      filter: brightness(.8);
      opacity: 1;
   }
   .arrow-previous {
      left: 0;
      top: 50%;
      transform: translateY(-50%);
   }
   .arrow-forward {
      right: 0;
      top: 50%;
      transform: translateY(-50%);
   }

   .exit-photo {
      z-index: 1;
      position: absolute;
      top: 0;
      right: 0;
      margin: 2rem;
      font-size: 2rem;
      color: #eee;
      background-color: transparent;
      border: none;
      outline: none;
      cursor: pointer;
   }
   .exit-photo:hover {
      color: #aaa;
   }

   .large-photo-box {
      position: relative;
      top: 50%;
      left: 50%;
      height: 90%;
      width: 90%;
      transform: translate(-50%, -50%);
   }
   .large-photo {
      position: relative;
      max-width: 100%;
      max-height: 100%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(.5);
   }
</style>