<template>
   <div class="art-container">
      <div class="art-show">
         <img class="large-art"
                        :src="require(`../assets/Art/Arts/art-${selectedArt}.png`)"
                        :alt="`Large Art ${selectedArt}`">
      </div>
      <div class="art-scrollbox">
         <!-- <img class="arrow arrow-previous"
                  src="../assets/Art/left-arrow.png"
                  alt="Arrow Previous"
                  @click="navPrevious()">
         <img class="arrow arrow-forward"
                  src="../assets/Art/right-arrow.png"
                  alt="Arrow Forward"
                  @click="navForward()"> -->
         <div class="art-nav">
            <div class="art-box" v-for="(art, index) in arts" :key="index">
               <img :class="`art art-${art}`"
                        :src="require(`../assets/Art/Arts/art-${art}.png`)"
                        :alt="`Art ${art}`"
                        @click="selectArt(art)">
            </div>
         </div>
      </div>
   </div>
</template>

<script>
export default {
   data() {
      return {
         arts: 10,
         selectedArt: 1
      }
   },
   methods: {
      selectArt(art) {
         this.selectedArt = art;
         TweenMax.fromTo('.large-art', 1, {y: '100rem', autoAlpha: 0.5}, {y: 0, x: '-50%', autoAlpha: 1, ease: Expo.easeOut});
      },
      navPrevious() {
         document.querySelector('.art-nav').style.left = '0%';
      },
      navForward() {
         document.querySelector('.art-nav').style.right = '0%';
      }
   }
}
</script>

<style scoped>
   .art-container {
      position: absolute;
      height: 90vh;
      width: 100%;
   }
   .art-scrollbox {
      position: absolute;
      width: 60rem;
      left: 50%;
      bottom: 8%;
      transform: translateX(-50%);
      box-shadow: 4px 4px 8px 2px rgba(0, 0, 0, 0.3);
      overflow-x: scroll;
   }
   .art-nav {
      position: relative;
      height: 8rem;
      width: 100rem;
      left: 0;
      display: flex;
      justify-content: space-evenly;
      align-items: center;
   }
   /* .arrow {
      z-index: 1;
      position: absolute;
      cursor: pointer;
      height: 3rem;
      filter: brightness(.5);
      opacity: .5;
      border-radius: 5%;
   }
   .arrow:hover {
      filter: brightness(.8);
      opacity: 1;
   }
   .arrow-previous {
      left: 0;
      top: 50%;
      transform: translateY(-50%);
   }
   .arrow-forward {
      right: 0;
      top: 50%;
      transform: translateY(-50%);
   } */

   .art {
      position: relative;
      height: 7rem;
      cursor: pointer;
      filter: brightness(.8);
   }
   .art:hover {
      filter: brightness(1);
   }

   .large-art {
      position: relative;
      margin-top: 3rem;
      left: 50%;
      height: 30rem;
      box-shadow: 3px 3px 8px 3px rgba(0, 0, 0, 0.3);
      transform: translate(-50%);
   }

   /********************************************/
   /* MEDIA QUERIES */
   /********************************************/

   @media only screen and (max-width: 1040px) {
      .art-scrollbox {
         width: 80%;
      }
   }

   @media only screen and (max-width: 720px) {
      .large-art {
         margin-top: 5rem;
         height: 20rem;
      }
   }

   @media only screen and (max-width: 500px) {
      .large-art {
         margin-top: 7rem;
         height: 15rem;
      }

      .art-nav {
         height: 7rem;
         width: 80rem;
      }

      .art {
         height: 6rem;
      }
   }
</style>
