      @mouseout="mouseOut('.expand-1', '.text-about', '.logo-about')"
      @click="aboutToggle(); reduceNavigation();"
    >
      <div class="expand expand-1"></div>
      <span class="text text-about">About</span>
    </div>
    <!-- Photo -->
    <div
      class="triangle triangle-2"
      @mouseover="mouseOver('.expand-2', '.text-camera', '.logo-camera')"
      @mouseout="mouseOut('.expand-2', '.text-camera', '.logo-camera')"
      @click="photoToggle(); reduceNavigation();"
    >
      <div class="expand expand-2"></div>
      <span class="text text-camera">Photo</span>
    </div>
    <!-- Art -->
    <div
      class="triangle triangle-3"
      @mouseover="mouseOver('.expand-3', '.text-art', '.logo-art')"
      @mouseout="mouseOut('.expand-3', '.text-art', '.logo-art')"
      @click="artToggle(); reduceNavigation();"
    >
      <div class="expand expand-3"></div>
      <span class="text text-art">Art</span>
    </div>
    <!-- Web -->
    <div
      class="triangle triangle-4"
      @mouseover="mouseOver('.expand-4', '.text-web', '.logo-web')"
      @mouseout="mouseOut('.expand-4', '.text-web', '.logo-web')"
