/////////////////////////////////////////////////////////////////////////////////////////////////////////

<script>
export default {
  data() {
    return {
        navigationToggle: false,
        currentPage: '',
        toggle: ''
    }
  },
  methods: {
    aboutToggle() {
      this.$store.commit('toggleAbout');
      TweenMax.to('.container', .3, {backgroundColor: '#ff0000'});
      this.currentPage = '.logo-about';
      this.toggle = 'toggleAbout'
    },
    photoToggle() {
      this.$store.commit('togglePhoto');
      TweenMax.to('.container', .3, {backgroundColor: '#22b6e2'});
      this.currentPage = '.logo-camera';
      this.toggle = 'togglePhoto'
    },
    artToggle() {
      this.$store.commit('toggleArt');
      TweenMax.to('.container', .3, {backgroundColor: '#d1512d'});
      this.currentPage = '.logo-art';
      this.toggle = 'toggleArt'
    },
    webToggle() {
      this.$store.commit('toggleWeb');
      TweenMax.to('.container', .3, {backgroundColor: '#9659a5'});
      this.currentPage = '.logo-web';
      this.toggle = 'toggleWeb'
    },
    otherToggle() {
      this.$store.commit('toggleOther');
      TweenMax.to('.container', .3, {backgroundColor: '#43b772'});
      this.currentPage = '.logo-other';
      this.toggle = 'toggleOther';
    },

    mouseOver(subject, text, svg) {
      TweenMax.to(subject, 0.3, {
        ease: Power2.easeIn,
        height: "200rem",
        width: "200rem"
      });
      TweenMax.to(text, 0.3, { color: "#fff" });
      TweenMax.to(svg, 0.1, { autoAlpha: 1 });
      TweenMax.to(".logo-name", 0.1, { autoAlpha: 0 });
    },
    mouseOut(subject, text, svg) {
      TweenMax.to(subject, 0.3, { height: "9rem", width: "10rem" });
      TweenMax.to(text, 0.3, { color: "#777" });
      TweenMax.to(svg, 0.1, { autoAlpha: 0 });
      TweenMax.to(".logo-name", 0.1, { autoAlpha: 1 });
    },

    reduceNavigation() {
      this.navigationToggle = true;

      TweenMax.to(['.triangle-1', '.triangle-2', '.triangle-3', '.triangle-4', '.triangle-5'], .1, {autoAlpha: 0});
      TweenMax.to('.container', .3, {height: '10vh'});
      TweenMax.to('.pentagon', .3, {height: '4rem'});
      TweenMax.to('.logo', .3, {height: '2rem'});
      TweenMax.to('.logo-name', .3, {autoAlpha: 0, delay: .3});
      TweenMax.to(this.currentPage, .3, {autoAlpha: 1, delay: .3});
    },
    navigationReturn() {
      if (this.navigationToggle) {
        this.navigationToggle = false;
        this.$store.commit(this.toggle);
        
        TweenMax.to('.container', .3, {backgroundColor: '#555'});
        TweenMax.to(['.triangle-1', '.triangle-2', '.triangle-3', '.triangle-4', '.triangle-5'], .3, {autoAlpha: 1});
        TweenMax.to('.container', .3, {height: '100vh'});
        TweenMax.to('.pentagon', .3, {height: '7rem'});
        TweenMax.to('.logo', .3, {height: '3rem'});
        TweenMax.to('.logo-name', .3, {autoAlpha: 1});
        TweenMax.to(this.currentPage, .3 ,{autoAlpha: 0});
      }
    }
  }
};
</script>

/////////////////////////////////////////////////////////////////////////////////////////////////////////

<style>
.container {
  position: relative;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  background-color: #555;
}

.portfolio-text {
  position: absolute;
  font-size: 18rem;
  top: 50%;
  left: 50%;
  color: rgba(68, 68, 68, 0.20);
  font-weight: 700;
  transform: translate(-50%, -50%) rotate(-15deg);
}

.text {
  position: absolute;
  font-size: 1.8rem;
  left: 50%;
  color: #777;
  transform: translate(-50%, 1910%) rotate(180deg);
}

.logo {
  z-index: 2;
  position: absolute;
  top: 50%;
  left: 50%;
  height: 3rem;
  transform: translate(-50%, -59%);
  opacity: 0;
  cursor: pointer;
}
.logo-name {
  opacity: 1;
}
.logo-about {
  visibility: hidden;
}
.logo-camera {
  visibility: hidden;
}
.logo-art {
  visibility: hidden;
}
.logo-web {
  visibility: hidden;
}
.logo-other {
  visibility: hidden;
}

.pentagon {
  z-index: 1;
  position: relative;
  top: 50%;
  left: 50%;
  height: 7rem;
  fill: white;
  transform: translate(-50%, -50%);
}

.triangle {
  position: absolute;
  overflow: hidden;
  top: 50%;
  left: 50%;
  width: 150rem;
  height: 150rem;
  clip-path: polygon(50% 31.2%, 0% 100%, 100% 100%);
}

.expand {
  position: absolute;
  top: 31%;
  left: 50%;
  width: 10rem;
  height: 9rem;
  transform: translate(-50%, -50%);
  cursor: pointer;
}

.triangle-1 {
  transform: translate(-50%, -68.95%) rotate(180deg); /* Translate up 68.8%-50%=18.8% from the 50% origin */
}
.triangle-2 {
  transform: translate(-32.13%, -55.98%) rotate(252deg);
}
.triangle-3 {
  transform: translate(-38.96%, -34.96%) rotate(324deg);
}
.triangle-4 {
  transform: translate(-61.05%, -34.96%) rotate(36deg);
}
.triangle-5 {
  transform: translate(-67.87%, -55.96%) rotate(108deg);
}
.expand-1 {
  background-color: #ff0000;
}
.expand-2 {
  background-color: #22b6e2;
}
.expand-3 {
  background-color: #d1512d;
}
.expand-4 {
  background-color: #9659a5;
}
.expand-5 {
  background-color: #43b772;
}

/********************************************/
/* MEDIA QUERIES */
/********************************************/

@media only screen and (max-width: 600px) {
  .portfolio-text {
    font-size: 12rem;
  }
}
</style>
