<template>
   <div class="about-container">
      <div class="card-container">
         <div class="flip-card">
            <div class="flip-card-inner flip-card-inner-1">
               <div class="flip-card-front card-front-1"
                        @click="flipCardInner('.flip-card-inner-1', '.flip-card-inner-2', '.flip-card-inner-3')"
                        @mouseover="hoverFlipAnimationIn('.info-shifter')"
                        @mouseout="hoverFlipAnimationOut('.info-shifter')">

                  <span class="info-text">Information</span>
                  <div class="shifter info-shifter" />
               </div>
               <div class="flip-card-back card-back-1"
                        @click="flipCardBack('.flip-card-inner-1', '.flip-card-inner-2', '.flip-card-inner-3')">

                  <img class="about-info" src="../assets/About/info.png" alt="About Info">
               </div>
            </div>
         </div>
         <div class="flip-card">
            <div class="flip-card-inner flip-card-inner-2">
               <div class="flip-card-front card-front-2"
                        @click="flipCardInner('.flip-card-inner-2', '.flip-card-inner-1', '.flip-card-inner-3')"
                        @mouseover="hoverFlipAnimationIn('.work-shifter')"
                        @mouseout="hoverFlipAnimationOut('.work-shifter')">

                  <span class="info-text">Work</span>
                  <div class="shifter work-shifter" />
               </div>
               <div class="flip-card-back card-back-2"
                        @click="flipCardBack('.flip-card-inner-2', '.flip-card-inner-1', '.flip-card-inner-3')">

                  <img class="about-info" src="../assets/About/work.png" alt="About Work">
               </div>
            </div>
         </div>
         <div class="flip-card">
            <div class="flip-card-inner flip-card-inner-3">
               <div class="flip-card-front card-front-3"
                        @click="flipCardInner('.flip-card-inner-3', '.flip-card-inner-1', '.flip-card-inner-2')"
                        @mouseover="hoverFlipAnimationIn('.skill-shifter')"
                        @mouseout="hoverFlipAnimationOut('.skill-shifter')">

                  <span class="info-text">Skill</span>
                  <div class="shifter skill-shifter" />
               </div>
               <div class="flip-card-back card-back-3"
                        @click="flipCardBack('.flip-card-inner-3', '.flip-card-inner-1', '.flip-card-inner-2')">

                  <img class="about-info" src="../assets/About/skill.png" alt="About Skill">
               </div>
            </div>
         </div>
      </div>
   </div>
</template>

/////////////////////////////////////////////////////////////////////////////////////////////////////////

<script>
export default {
   methods: {
      flipCardInner(cardInner, cardBack1, cardBack2) {
         TweenMax.to(cardInner, .1, {rotationY: '180deg'});
         TweenMax.to(cardBack1, .1, {rotationY: '0deg'});
         TweenMax.to(cardBack2, .1, {rotationY: '0deg'});
      } ,
      flipCardBack(cardInner) {
         TweenMax.to(cardInner, .1, {rotationY: '0deg'});
         TweenMax.to(cardBack1, .1, {rotationY: '0deg'});
         TweenMax.to(cardBack2, .1, {rotationY: '0deg'});
      },
      hoverFlipAnimationIn(objShifter) {
         document.querySelector(objShifter).classList.add('play');
         document.querySelector(objShifter).classList.remove('reverse');
      },
      hoverFlipAnimationOut(objShifter) {
         document.querySelector(objShifter).classList.add('reverse');
         document.querySelector(objShifter).classList.remove('play');
      }
   }
}
</script>

/////////////////////////////////////////////////////////////////////////////////////////////////////////

<style>
   .about-container {
      position: relative;
      height: 90vh;
      width: 100%;
      overflow: hidden;
   }

   .card-container {
      position: absolute;
      top: 50%;
      left: 50%;
      display: flex;
      justify-content: space-between;
      transform: translate(-50%, -50%);
   }

/******************************************************************/

   .flip-card {
      background-color: transparent;
      margin: 10px;
      height: 35rem;
      width: 25rem;
      perspective: 1000px;
   }
   .flip-card-inner {
      position: relative;
      width: 100%;
      height: 100%;
      text-align: center;
      transition: transform 0.6s;
      transform-style: preserve-3d;
      box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
   }
   .flip-card-front, .flip-card-back {
      position: absolute;
      width: 100%;
      height: 100%;
      backface-visibility: hidden;
   }
   .flip-card-front {
      cursor: pointer;
   }
   .flip-card-back {
      cursor: pointer;
      transform: rotateY(180deg);
   }
   .card-front-1, .card-back-1 {
      background-color: #ff4d4d;
   }
   .card-front-2, .card-back-2 {
      background-color: #ff8364;
   }
   .card-front-3, .card-back-3 {
      background-color: #ffb471;
   }

/******************************************************************/

   .about-info {
      height: 100%;
      width: 100%;
   }

/******************************************************************/

   .info-text {
      position: relative;
      display: inline-block;
      margin-top: 4rem;
      font-size: 3rem;
      color: #f6edcf;
   }

/******************************************************************/

   @keyframes in {
      0% {
         background-position: 0px 0px;
      }
      100% {
         background-position: -288rem 0px;   /* -720px 0px */
      }
   }
   @keyframes out {
      0% {
         background-position: -288rem 0px;   /* -720px 0px */
      }
      100% {
         background-position: -0px 0px;
      }
   }
   .shifter {
      position: relative;
      margin-top: 5rem;
      background-size: 297.6rem 9.6rem;   /* 744px 24px */
      animation-duration: 400ms;
      animation-timing-function: steps(30);
      animation-fill-mode: forwards;
      width: 9.6rem;   /* 24px */
      height: 9.6rem;   /* 24px */
      background-repeat: no-repeat;
      left: 50%;
      transform: translateX(-50%);
   }
   .shifter.play {
      animation-name: in;
   }
   .shifter.reverse {
      animation-name: out;
   }
   .info-shifter {
      background-image: url("../assets/About/Info/infoSVG.svg");
   }
   .work-shifter {
      background-image: url("../assets/About/Info/workSVG.svg");
   }
   .skill-shifter {
      background-image: url("../assets/About/Info/skillSVG.svg");
   }

   /********************************************/
   /* MEDIA QUERIES */
   /********************************************/

   @media only screen and (max-width: 1280px) {
      .flip-card {
         height: 30rem;
         width: 20rem;
      }
   }

   @media only screen and (max-width: 1033px) {
      .about-container {
         height: initial;
         overflow: initial;
      }

      .card-container {
         top: initial;
         left: 50%;
         flex-direction: column;
         transform: translateX(-50%);
      }

      .flip-card {
         height: 40rem;
         width: 30rem;
         margin: 40px;
      }

      .shifter {
         margin-top: 8rem;
      }

      .info-text {
         font-size: 4rem;
      }
   }

   @media only screen and (max-width: 600px) {
      .flip-card {
         height: 27rem;
         width: 17rem;
      }

      .info-text {
         font-size: 2rem;
      }

      .shifter {
         margin-top: 4rem;
      }
   }

   @media only screen and (max-width: 360px) {
      .flip-card {
         height: 25rem;
         width: 15rem;
      }

      .info-text {
         font-size: 2rem;
      }

      .shifter {
         margin-top: 2rem;
      }
   }
</style>

