import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    aboutToggle: false,
    photoToggle: false,
    artToggle: false,
    webToggle: false,
    otherToggle: false
  },
  mutations: {
    toggleAbout(state) {
      state.aboutToggle = !state.aboutToggle
    },
    togglePhoto(state) {
      state.photoToggle = !state.photoToggle
    },
    toggleArt(state) {
      state.artToggle = !state.artToggle
    },
    toggleWeb(state) {
      state.webToggle = !state.webToggle
    },
    toggleOther(state) {
      state.otherToggle = !state.otherToggle
    }
  }
})