<template>
  <div>
    <app-home></app-home>
    <app-about v-if="toggleAbout"></app-about>
    <app-photo v-if="togglePhoto"></app-photo>
    <app-art v-if="toggleArt"></app-art>
    <app-web v-if="toggleWeb"></app-web>
    <app-other v-if="toggleOther"></app-other>
  </div>
</template>

<script>
import Home from "./components/Home.vue";
import About from "./components/About.vue";
import Photo from "./components/Photo.vue";
import Art from "./components/Art.vue";
import Web from "./components/Web.vue";
import Other from "./components/Other.vue";

export default {
  components: {
    appHome: Home,
    appAbout: About,
    appPhoto: Photo,
    appArt: Art,
    appWeb: Web,
    appOther: Other
  },
  computed: {
    toggleAbout() {
      return this.$store.state.aboutToggle;
    },
    togglePhoto() {
      return this.$store.state.photoToggle;
    },
    toggleArt() {
      return this.$store.state.artToggle;
    },
    toggleWeb() {
      return this.$store.state.webToggle;
    },
    toggleOther() {
      return this.$store.state.otherToggle;
    }
  }
};
</script>

